<template>
  <form action="">
    <div class="text item">
      <div slot="tip" class="el-upload__tip">Logotipo del Panel de acceso</div>
      <div class="margin-30">
        <el-upload
          v-loading="loading"
          class="avatar-uploader"
          drag
          name="image"
          action=""
          :show-file-list="false"
          :http-request="uploadImage"
          :before-upload="beforeUpload"
        >
          <img
            v-if="settingsStore.login.logo"
            :src="configStore.baseUrl + settingsStore.login.logo"
            class="image-seo"
          />

          <template v-else>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">
              Suelta tu foto aquí o
              <em>haz clic para cargar</em>
            </div>
          </template>
          <div slot="tip" class="el-upload__tip">
            Solo imagenes con un peso menor a 10MB
          </div>
        </el-upload>
      </div>
    </div>
    <el-row :gutter="10" type="flex" justify="between">
      <div class="margin-20">
        <span class="demonstration">Color para El panel de Acceso</span>
        <br />
        <el-color-picker v-model="settingsStore.login.color"></el-color-picker>
      </div>
    </el-row>
    <el-button :loading="settingsStore.loading" type="primary" @click="update"
      >Actualizar</el-button
    >
  </form>
</template>

<script>
// import { storeToRefs } from 'pinia'

import { store as storeImageFile } from '@/api/images'

import { useStoreSettings } from '../../stores/settingsStore'

import { useStoreConfig } from '../../stores/configStore'

const settingsStore = useStoreSettings()

const configStore = useStoreConfig()
export default {
  name: 'Login',
  data () {
    // const { sideBar, loading } = storeToRefs(settingsStore)
    // const { sideBar } = settingsStore

    return {
      configStore,
      settingsStore,
      loading: false
    }
  },
  async created () {
    this.loading = true
    // const settingsStore = useStoreSettings()
    settingsStore.fetchLogin()
    this.loading = false
  },
  methods: {
    async update () {
      settingsStore.updateLogin()
    },
    async uploadImage (param) {
      this.loading = true
      const name = param.file.name
      const filename = name.split('.').slice(0, -1).join('.')
      const extension = name.split('.').pop()
      const formData = new FormData()
      formData.append('name', filename)
      formData.append('thumbName', 'thumb-' + filename)
      formData.append('originalName', name)
      formData.append('alt', 'Logo Manager')
      formData.append('title', 'Logo')
      formData.append('extension', param.file.type)
      formData.append('orientation', 'horizontal')
      formData.append('format', extension)
      formData.append('isPublic', 1)
      formData.append('image', param.file)
      storeImageFile(formData)
        .then(response => {
          settingsStore.login.logo = response.data.pathFile
          param.onSuccess(response.data.pathFile)
        })
        .catch(response => {
          // console.log(response)
          param.onError()
        }).finally(() => (this.loading = false))
    },
    beforeUpload (file) {
      // console.log(file)
      const isJPG = file.type === 'image/jpeg'
      const isPNG = file.type === 'image/png'
      const isLt10M = file.size / 1024 / 1024 < 10
      var isImage = true
      if (!isJPG && !isPNG) {
        this.$message.error('¡El archivo debe ser una imagen!')
        isImage = false
      }
      if (!isLt10M) {
        this.$message.error('¡El archivo excede los 10MB!')
      }
      return isImage && isLt10M
    }
  }
}
</script>

<style lang="scss" scoped>

.avatar-uploader{
  width: 500px;
}

</style>
